<template>
  <div class='login_container'>
      <div class="login_box">
        <!-- 登录logo区 -->
        <div class="avatar_box">
          <img src="../assets/logo.png" alt="">
        </div>

        <!-- 登录表单区域 -->
        <el-form ref="loginFromRef" class="login_from" :model="loginForm" :rules='loginFromRules'>
          <!-- 用户名 -->
          <el-form-item prop="username">
            <el-input v-model='loginForm.username' prefix-icon="iconfont icon-user" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <!-- 密码 -->
          <el-form-item prop="password">
            <el-input v-model='loginForm.password' prefix-icon="iconfont icon-password" type="password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <!-- 按钮区 -->
          <el-form-item class="login_btns">
            <el-button type="primary" @click="vaildLoginForm">登录</el-button>
            <el-button type="info" @click="resetLoginForm">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
  </div>
</template>

<script>

export default {
  name: 'Login',
  props: {

  },
  data () {
    return {
      // 这是登录表单的数据绑定对象
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      loginFromRules: {
        // 验证用户名是否合理
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
        ],
        // 验证密码是否合理
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginForm () {
      this.$refs.loginFromRef.resetFields()
    },
    vaildLoginForm () {
      this.$refs.loginFromRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error('登录失败')
        this.$message.success('登录成功')
        // 将用户登录后的token， 保存到sessionStorage中
        window.sessionStorage.setItem('token', res.data.token)
        // 通过编程式导航跳转到后台主页，地址为'/home'
        this.$router.push('/home')
      })
    }
  }
}
</script>
<style scoped>
.login_container{
  background-color: #2b4b6b;
  height: 100%;
}
.login_box{
  width: 450px;
  height: 300px;
  background-color: #ffffff;
  border-radius: 4px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.avatar_box{
  height: 130px;
  width: 130px;
  line-height: 130px;
  border: 1px solid #DCDFE6 ;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #E4E7ED ;
  background-color: #ffffff;
  /* 定位 */
  position: absolute;
  left: 50%;
  transform: translate(-50%,-50%);
}
.avatar_box img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #DCDFE6;
}
.login_from{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
}
.login_btns{
  display: flex;
  justify-content: flex-end;
}
</style>
